@Component({
    selector: 'bh-home',
    templateUrl: './home.template.html'
})

export class homeComponent extends NBaseComponent implements OnInit {

    username:string = ''
    email:string = ''
    password:string = ''
    isLoading:boolean = false


    constructor(private router: Router, private dataService:dataservice) {
        super();
    }

    ngOnInit() {

    }

    async onLogin(){
        if(!this.email){
            return
        }

        if(!this.password){
            return
        }

        try {
            console.log('email: ', this.email)
            console.log('password:', this.password)
            this.isLoading = true

            let bh = await this.dataService.login(this.email, this.password)
            if(bh.local.result && bh.local.result.user){
                let {name, email, phone, department, region} = bh.local.result.user
                console.log({name, email, phone, department, region})

                window.localStorage.setItem('name', name)
                window.localStorage.setItem('email', email)
                window.localStorage.setItem('phone', phone)
                window.localStorage.setItem('department', department)
                window.localStorage.setItem('region', region)
                window.localStorage.setItem('HAS_FEVER', '')
                window.localStorage.setItem('HAS_CONTACT', '')
                window.localStorage.setItem('HAS_TRAVELED', '')
                window.localStorage.setItem('HEALTH_CONDITION', '')
                this.isLoading = false

                if(email === 'hradmin@ingredion.com' || email === 'hradmin@pbfenergy.com'){
                    this.router.navigate(['/dashboard'])
                    return
                }

                this.router.navigate(['/personal-info'])
            }

        } catch(err){
            console.log(err)
            this.isLoading = false
        }
    }


}
