import { Component, OnInit } from '@angular/core'
import { NBaseComponent } from '../../../../../app/baseClasses/nBase.component';

import {dataservice} from 'app/sd-services/dataservice';


/*
Client Service import Example:
import { servicename } from 'app/sd-services/servicename';
*/

/*
Legacy Service import Example :
import { HeroService } from '../../services/hero/hero.service';
*/

@Component({
    selector: 'bh-dashboard',
    templateUrl: './dashboard.template.html'
})

export class dashboardComponent extends NBaseComponent implements OnInit {

    name:string = 'John Doe'; 
    region:string = 'Westchester';

    isLoading:boolean = true;

    feverPositiveCount:number = 0;
    feverNegativeCount:number = 0;
    contactPositiveCount:number = 0;
    contactNegativeCount:number = 0;
    healthPositiveCount:number = 0;
    healthNegativeCount:number = 0;
    abroadPositiveCount:number = 0;
    abroadNegativeCount:number = 0;


    constructor(private dataService:dataservice) {
        super();
    }

    ngOnInit() {
        this.name = window.localStorage.getItem('name')
        this.region = window.localStorage.getItem('region')

        this.getAllData()
    }


    async getAllData(){
        
        try {
            this.isLoading = true
            let bh = await this.dataService.getData()
            if(bh.local.result && bh.local.result.user){
                let res = bh.local.result.user;

                res.forEach(d => {
                    if(d.fever === "Yes" || d.fever === "yes" || d.fever === "y" || d.fever === "Y" || d.fever === "true"){
                        this.feverPositiveCount = this.feverPositiveCount + 1
                    } else {
                        this.feverNegativeCount = this.feverNegativeCount + 1
                    }

                    if(d.contact === "Yes" || d.contact === "yes" || d.contact === "y" || d.contact === "Y" || d.contact === "true"){
                        this.contactPositiveCount = this.contactPositiveCount + 1
                    } else {
                        this.contactNegativeCount = this.contactNegativeCount + 1
                    }

                    if(d.health_condition === "Yes" || d.health_condition === "yes" || d.health_condition === "y" || d.health_condition === "Y" || d.health_condition === "true"){
                        this.healthPositiveCount = this.healthPositiveCount + 1
                    } else {
                        this.healthNegativeCount = this.healthNegativeCount + 1
                    }

                    if(d.foreign_tour === "Yes" || d.foreign_tour === "yes" || d.foreign_tour === "y" || d.foreign_tour === "Y" || d.foreign_tour === "true"){
                        this.abroadPositiveCount = this.abroadPositiveCount + 1
                    } else {
                        this.abroadNegativeCount = this.abroadNegativeCount + 1
                    }

                })
                this.isLoading = false
            }



        } catch(err){
            console.log(err)
            this.isLoading = false
        }
    }









}
