@Component({
    selector: 'bh-questions',
    templateUrl: './questions.template.html'
})

export class questionsComponent extends NBaseComponent implements OnInit {

    @Input() question:string;
    @Input() radioValue:string;
    @Input() questionIndex:number;

    @Output() onAnswer: EventEmitter<any> = new EventEmitter();


    answer:string = ""




    onCheckYes(){
        console.log('onCheckYes callled ....')
    }




    constructor() {
        super();
    }

    ngOnInit() {
        if(this.questionIndex === 0){
            let hasFever = window.localStorage.getItem('HAS_FEVER')
            if(hasFever){
                this.answer = hasFever
            }
            return
        }

        if(this.questionIndex === 1){
            let hasContact = window.localStorage.getItem('HAS_CONTACT')
            if(hasContact){
                this.answer = hasContact
            }
            return
        }

        if(this.questionIndex === 2){
            let hasTraveled = window.localStorage.getItem('HAS_TRAVELED')
            if(hasTraveled){
                this.answer = hasTraveled
            }
            return
        }

        if(this.questionIndex === 3){
            let healthCondition = window.localStorage.getItem('HEALTH_CONDITION')
            if(healthCondition){
                this.answer = healthCondition
            }
            return
        }


    }

    onChangeRadio(e, questionIndex){
        console.log('onChangeRadio called...')
        console.log("Question Index", questionIndex)
        let val = e.value
        this.answer = val
        console.log({val})

        if(questionIndex === 0){
            window.localStorage.setItem('HAS_FEVER', val)
            return
        }        

        if(questionIndex === 1){
            window.localStorage.setItem('HAS_CONTACT', val)
            return
        }       

        if(questionIndex === 2){
            window.localStorage.setItem('HAS_TRAVELED', val)
            return
        }

        if(questionIndex === 3){
            window.localStorage.setItem('HEALTH_CONDITION', val)
            return
        }

    }


}
