@Component({
    selector: 'bh-details',
    templateUrl: './details.template.html'
})

export class detailsComponent extends NBaseComponent implements OnInit {

    name:string = ''
    phone:string = '9876543210'
    email:string = 'john.doe@ingredion.com'
    age:string = "10"
    department:string = 'Accounts'
    region:string = 'EMEA'
    fever:string = ""
    contact:string = ""
    foreignTour:string = ""

    isDiabetes:boolean = false
    isHyperTension:boolean = false
    isLungDisease:boolean = false
    isHeartDisease:boolean = false
    isNone:boolean = false




    isSaving:boolean = false


    constructor(private router: Router, private dataService:dataservice, private activatedRoute: ActivatedRoute) {
        super();
    }

    ngOnInit() {
        let username = this.activatedRoute.snapshot.paramMap.get("username");
        if(username){
            this.name = username;
        }
    }

    async saveData(){
        let healtCondition = []
        if(this.isDiabetes){
            healtCondition.push("Diabetes")
        }

        if(this.isHyperTension){
            healtCondition.push("Hypertension")
        }

        if(this.isLungDisease){
            healtCondition.push("Lung Disease")
        }

        if(this.isHeartDisease){
            healtCondition.push("Heart Disease")
        }

        if(this.isNone){
            healtCondition= ["None"]
        }

        let data = {
            name: this.name,
            phone: this.phone,
            email: this.email,
            age: this.age,
            department: this.department,
            region: this.region,
            fever: this.fever,
            contact: this.contact,
            foreign_tour: this.foreignTour,
            health_condition: healtCondition
        }

        try {
            this.isSaving = true
            let bh = await this.dataService.saveData(data)
            console.log(bh.local.result)
            this.isSaving = false

            // this.name = JSON.stringify(bh.local.result)
            // this.phone = JSON.stringify(bh.local.result)


            this.router.navigate(['/success-page'])


        } catch(err){
            console.error(err)
        }



    }



}
